'use client'

import { useEffect, useState } from 'react'
import type { Metadata, Viewport } from 'next'
import { Inter } from 'next/font/google'
import { AnimatePresence, motion } from 'framer-motion'
import './globals.css'
import ClientOnly from '@/components/ClientOnly'
import { PageTransition } from '@/components/ui/page-transition'

// Configure the Inter font with basic settings
const inter = Inter({
  weight: ['400', '500', '600', '700'],
  subsets: ['latin'],
  display: 'swap',
  variable: '--font-inter',
  fallback: ['system-ui', 'sans-serif'],
  preload: true,
  adjustFontFallback: false
})

export const metadata: Metadata = {
  title: 'SLA Fusion - Mescle Planilhas Excel Profissionalmente',
  description: 'Plataforma profissional para fusão de planilhas Excel de SLA do Mercado Livre. Rápido, simples e intuitivo. Suporte a arquivos grandes, filtros automáticos, gráficos dinâmicos e ranking de atrasos.',
  keywords: ['excel', 'planilhas', 'mesclar', 'sla', 'mercado livre', 'fusão', 'dados'],
  authors: [{ name: 'SLA Fusion Team' }],
  openGraph: {
    title: 'SLA Fusion - Mescle Planilhas Excel Profissionalmente',
    description: 'Plataforma profissional para fusão de planilhas Excel. Suporte a arquivos grandes, filtros automáticos e análise visual.',
    type: 'website',
  },
    generator: 'v0.app'
}

export const viewport: Viewport = {
  themeColor: '#0ea5e9',
  width: 'device-width',
  initialScale: 1,
  maximumScale: 5,
  userScalable: true,
}

export default function RootLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const [isLoading, setIsLoading] = useState(true)
  const [isFirstLoad, setIsFirstLoad] = useState(true)

  useEffect(() => {
    // Simulate content loading
    const timer = setTimeout(() => {
      setIsLoading(false)
      
      // After first load, we can show the page transition
      if (isFirstLoad) {
        const timer2 = setTimeout(() => {
          setIsFirstLoad(false)
        }, 500)
        return () => clearTimeout(timer2)
      }
    }, 1000)

    return () => clearTimeout(timer)
  }, [isFirstLoad])

  // Loading animation
  if (isLoading) {
    return (
      <html lang="pt-BR" suppressHydrationWarning>
        <body className={`${inter.className} bg-background flex items-center justify-center min-h-screen`}>
          <motion.div
            initial={{ scale: 0.9, opacity: 0 }}
            animate={{ scale: 1, opacity: 1 }}
            className="text-center"
          >
            <motion.div
              className="w-16 h-16 border-4 border-primary border-t-transparent rounded-full mx-auto mb-4"
              animate={{ rotate: 360 }}
              transition={{ repeat: Infinity, duration: 1, ease: 'linear' }}
            />
            <motion.p
              initial={{ y: 20, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ delay: 0.2 }}
              className="text-foreground font-medium"
            >
              Carregando...
            </motion.p>
          </motion.div>
        </body>
      </html>
    )
  }

  return (
    <html lang="pt-BR" suppressHydrationWarning>
      <body className={inter.className} suppressHydrationWarning>
        <ClientOnly>
          {isFirstLoad ? (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ duration: 0.5 }}
            >
              {children}
            </motion.div>
          ) : (
            <PageTransition>
              {children}
            </PageTransition>
          )}
        </ClientOnly>
      </body>
    </html>
  )
}
